
.coin-search {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 64px;
 
}
.ranhead{
  font-size: 30px;
  color: #4801ff;
}
.coin-text {
  margin-bottom: 32px;
  text-align: center;
}

.coin-input {
  width: 300px;
  height: 50px;
  border-radius: 4px;
  border: none;
  background-image: linear-gradient(
    -225deg,
    #ac32e4 0%,
    #7918f2 48%,
    #4801ff 100%
  );

  color: #e2e2e2;
}

.coin-input::placeholder {
  color: #e2e2e2;
}